const state = {
	areaInfo: {},
	invoiceInfo: {
		type: 0,
		title: '',
		taxNo: ''
	},
	// 
	selfPickInfo: {},
	isStatus: false, // , 
};
const mutations = {
	setArea(state, data) {
		state.areaInfo = data
	},
	
	setInvoice(state, data) {
		state.invoiceInfo = data;
	},
	
	setFrom(state, data) {
		state.isStatus = data
	},
	
	getSelfPick(state, data) {
		state.selfPickInfo = data;
	}
	
};
const actions = {
	// 
	setAddress({ commit }, data) {
		commit('setArea', data)
	},
	// 
	getFromOrder({commit}, data) {
		commit('setFrom', data)
	},
	
	// 
	setSelfPick({commit}, data) {
		commit('getSelfPick', data)
	}
}

export default {
	namespaced: true,
	state,
	mutations,
	actions
}