import { getCache } from '@/utils/storage';
import store from '@/store';
import { API_URL } from '@/env';

// 
export let isRelogin = { show: false };

// vmvuex
module.exports = (vm) => {
	// 
	uni.$u.http.setConfig((config) => {
		/* config */
		config.baseURL = API_URL; /*  */
		config.timeout = 10000;
		config.header = {
			'Content-Type': 'application/json;charset=utf-8',
		};
		return config
	})

	// 
	uni.$u.http.interceptors.request.use((config) => { // async await 
		//  token
		config.data = config.data || {};
		const isToken = (config.header || {}).isToken === false;
		const storageTokan = getCache('token');
		const token = vm.$store.state.Login.token || storageTokan;
		// token 
		if (token && !isToken) {
			config.header['token'] = token;
		};
		
		// token
		if(isToken) {
			return config;
		}

		return config
	}, config => { // async await 
		return Promise.reject(config)
	})

	// 
	uni.$u.http.interceptors.response.use((res) => {
		// 
		const code = res.data.code || 200;
		// 
		const msg = res.data.message;

	if (code === 401 || code === 10010) {
			store.dispatch('Login/loginAgain').then(res=> {
				uni.reLaunch({
					url: '/pages/login/login'
				});
			});
			return res.data;
		} else if (code === 500) {
			uni.$u.toast(msg)
			return Promise.reject(new Error(msg))
		} else if(code == 60069 || code == 60070) {
			return res.data;
		} else if (code !== 200) {
			uni.$u.toast(msg)
			return Promise.reject('error')
		} else {
			return res.data;
		}
	},
		error => {
			let { message } = error;
			if (message == "Network Error") {
				message = "";
			}
			else if (message.includes("timeout")) {
				message = "";
			}
			else if (message.includes("Request failed with status code")) {
				message = "" + message.substr(message.length - 3) + "";
			}
			uni.$u.toast(message)
			return Promise.reject(error)
		}
	)
}